import React, { useEffect, useState } from "react";
import axios from "axios";
import "./css/SavedNote.css";

const SavedNote = ({ setFooterBottom, authEmail }) => {
  const [blackNotesList, setBlackNotesList] = useState([]);

  const [editNotes, setEditNotes] = useState({
    name: "",
    note: "",
    date: "",
    day: "",
    id: "",
  });
  const [editId, setEditId] = useState();
  useEffect(() => {
    getNotes();
  }, []);

  const current = new Date();
  const date = `${current.getDate()}/${current.getMonth() + 1
    }/${current.getFullYear()}`;
  const dayNum = current.getDay();
  var day;
  if (dayNum === 0) {
    day = "Sunday";
  } else if (dayNum === 1) {
    day = "Monday";
  } else if (dayNum === 2) {
    day = "Tuesday";
  } else if (dayNum === 3) {
    day = "Wednseday";
  } else if (dayNum === 4) {
    day = "Thursday";
  } else if (dayNum === 5) {
    day = "Friday";
  } else if (dayNum === 6) {
    day = "Sutarday";
  }

  const getNotes = () => {
    axios
      .post("/api/getNotes", { email: authEmail })
      .then((res) => {
        setBlackNotesList(res.data);

        if (res.data.length === 0) {
          setFooterBottom({
            marginTop: "77vh",
          });
        } else {
          setFooterBottom({
            marginTop: "16vh",
          });
        }
      });
  };

  const deleteInote = (id, email) => {
    axios
      .post("/api/deleteNotes", { id, email })
      .then((res) => {
        setBlackNotesList(res.data);
        if (res.data.length === 0) {
          setFooterBottom({
            marginTop: "77vh",
          });
        } else {
          setFooterBottom({
            marginTop: "16vh",
          });
        }
      });
    alert("Delete");
  };
  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditNotes({
      ...editNotes,
      [name]: value,
      date: date,
      day: day,
    });
  };
  const edit = (id, title, note) => {
    setEditId(id);
    setEditNotes({
      title: title,
      note: note,
      id: id,
    });
  };
  const saveEdit = () => {
    axios.post("/api/editNote", editNotes);
    alert("Save Edits");
    getNotes();
  };

  return (
    <>
      <div>
        <hr className="hr-dec" />
        <div className="saveNote row">
          {blackNotesList.map((notes, index) => (

            <div className="inote-Card col-md-3" key={index}>
              <p className="get-date">
                {notes.day} - {notes.date}
              </p>
              <hr />
              <div className="d-flex justify-content-between">
                <h5 className="get-title">
                  {notes.title.slice(0, 10) + "..."}
                </h5>
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    margin: "0px 30px 0px 0px",
                  }}
                >
                  <svg
                    onClick={() => edit(notes._id, notes.title, notes.note)}
                    data-bs-toggle="modal"
                    data-bs-target="#exampleModal"
                    xmlns="http://www.w3.org/2000/svg"
                    width="23"
                    height="23"
                    fill="currentColor"
                    className="bi bi-pencil-square iconHover"
                    viewBox="0 0 16 16"
                    style={{
                      marginTop: "1px",
                      marginRight: "25px",
                      color: "#2479c3",
                      cursor: "pointer",
                    }}
                  >
                    <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z" />
                    <path
                      fillRule="evenodd"
                      d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"
                    />
                  </svg>
                  <i
                    className="fa-solid fa-trash dlt"
                    aria-hidden="true"
                    onClick={() => deleteInote(notes._id, notes.note_Key)}
                  ></i>
                </div>
              </div>
              <hr />
              <textarea
                style={{
                  color: notes.color,
                  fontStyle: notes.fontStyle,
                  fontWeight: notes.fontWeight,
                }}
                className="decp-box"
                placeholder="Write here"
                value={notes.note}
                readOnly
              />
            </div>

          ))}
        </div>
      </div>

      <div
        className="modal fade"
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-lg" style={{ pointerEvents: "all" }}>
          {blackNotesList.map((note, index) => {
            return editId === note._id ? (
              <div className="editNote" key={index}>
                <div className="d-flex justify-content-between">
                  <input
                    type="text"
                    className="inpBox title-inp"
                    name="title"
                    placeholder="Add Title"
                    onChange={handleChange}
                    // valu={notes.title}
                    suppressContentEditableWarning={true}
                    defaultValue={note.title}
                  />
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      margin: "0px 25px",
                    }}
                  >
                    <p className="currentDate" style={{ marginRight: "40px" }}>
                      {day} - {date}
                    </p>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      x="0px"
                      y="0px"
                      width="26"
                      height="26"
                      viewBox="0 0 26 26"
                      data-bs-dismiss="modal"
                      aria-label="Close"
                      style={{ fill: "rgb(0 0 0)", cursor: "pointer" }}
                    >
                      <path d="M 21.734375 19.640625 L 19.636719 21.734375 C 19.253906 22.121094 18.628906 22.121094 18.242188 21.734375 L 13 16.496094 L 7.761719 21.734375 C 7.375 22.121094 6.746094 22.121094 6.363281 21.734375 L 4.265625 19.640625 C 3.878906 19.253906 3.878906 18.628906 4.265625 18.242188 L 9.503906 13 L 4.265625 7.761719 C 3.882813 7.371094 3.882813 6.742188 4.265625 6.363281 L 6.363281 4.265625 C 6.746094 3.878906 7.375 3.878906 7.761719 4.265625 L 13 9.507813 L 18.242188 4.265625 C 18.628906 3.878906 19.257813 3.878906 19.636719 4.265625 L 21.734375 6.359375 C 22.121094 6.746094 22.121094 7.375 21.738281 7.761719 L 16.496094 13 L 21.734375 18.242188 C 22.121094 18.628906 22.121094 19.253906 21.734375 19.640625 Z"></path>
                    </svg>
                  </div>
                </div>
                <textarea
                  style={{
                    color: note.color,
                    fontStyle: note.fontStyle,
                    fontWeight: note.fontWeight,
                    fontSize: note.fontSize,
                  }}
                  name="note"
                  className="note-dec"
                  placeholder="Add a description"
                  onChange={handleChange}
                  defaultValue={note.note}
                  // value={note.note}
                  suppressContentEditableWarning={true}
                ></textarea>
                <div className="addBtn" onClick={saveEdit}>
                  Save
                </div>
              </div>
            ) : (
              ""
            );
          })}
        </div>
      </div>
    </>
  );
};

export default SavedNote;
