import axios from "axios";
import React from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./css/Register.css";
const Register = () => {
  const navigate = useNavigate();
  const [spinner, setSpinner] = useState('d-none');
  const [userInfo, setUserInfo] = useState({
    name: "",
    email: "",
    password: "",
    cpassword: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserInfo({
      ...userInfo,
      [name]: value,
    });
  };

  const register = () => {
    const { name, email, password, cpassword } = userInfo;
    if (name && email && password && password === cpassword) {
      setSpinner('d-block')
      axios.post("/api/register", userInfo).then((res) => {
        if (res.data.error) {
          setSpinner('d-none')
          alert("Not registered")
        } else {
          navigate("/");
          setSpinner('d-none')
        }
      });
    } else {
      alert("invalid Input");
    }
  };

  return (
    <>

      <div className={`cover-spin ${spinner}`}>
        <div className="text-center position-absolute  top-50 start-50 translate-middle">
          <div className="spinner-border" role="status">
            <span className="visually-hidden"></span>
          </div>
          <p>Register...</p>
        </div>
      </div>


      <div className="container">
        <form className="register">
          <h1>Register</h1>
          <input
            onChange={handleChange}
            className="RegisterInp"
            name="name"
            value={userInfo.name}
            type="text"
            placeholder="Enter your name"
          />
          <input
            onChange={handleChange}
            className="RegisterInp"
            name="email"
            value={userInfo.email}
            type="email"
            placeholder="Enter your Email"
          />
          <input
            onChange={handleChange}
            className="RegisterInp"
            name="password"
            value={userInfo.password}
            type="password"
            placeholder="your password"
          />
          <input
            onChange={handleChange}
            className="RegisterInp"
            name="cpassword"
            value={userInfo.cpassword}
            type="password"
            placeholder="Re-Enter password"
          />
          <div onClick={register} className="btn my-4">
            Register
          </div>
          <p>or</p>
          <a onClick={() => navigate("/")} className="mb-4" style={{ cursor: "pointer" }} >
            Login &rArr;
          </a>
        </form>
      </div>
    </>
  );
};

export default Register;
