import "./css/Navbar.css";
import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import Cookies from "js-cookie";
import logo1 from "./photos/yournote.png";
import axios from "axios";

const Navbar = ({ setAuthEmail }) => {
  const navigate = useNavigate();
  const [spinner, setSpinner] = useState('d-none');
  const [userInfo, setUserInfo] = useState({});

  useEffect(() => {
    axios
      .get("/api/getdata", { withCredentials: true })
      .then((res) => {
        setUserInfo(res.data);
        setAuthEmail(res.data.email)
      });
  }, []);

  const logoutBtn = () => {
    Cookies.remove("jwtoken");
    setSpinner('d-block')
    navigate("/");
    window.location.reload();
  };

  const Routing = () => {
    if (!document.cookie) {
      return (
        <nav className="navbar navbar-expand-lg bg-light">
          <div className="container-fluid d-flex">
            <div className="p-2 flex-grow-1">
              <Link className="navbar-brand font-monospace" to="/">
                <img width="150" src={logo1} alt="" />
              </Link>
            </div>
          </div>
        </nav>
      );
    } else {
      return (
        <nav className="navbar navbar-expand-lg bg-light">
          <div className="container-fluid d-flex">
            <div className="p-2 flex-grow-1">
              <Link className="navbar-brand font-monospace" to="/">
                <img width="150" src={logo1} alt="" />
              </Link>
            </div>
            <div className="p-2 navContent " id="">
              <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                <li className="nav-item mx-2 ">
                  <Link
                    className="nav-link text-dark fw-bolder font-monospace"
                    aria-current="page"
                    to="/"
                  >
                    Home
                  </Link>
                </li>
                <li className="nav-item mx-2">
                  <Link
                    className="nav-link text-dark fw-bolder font-monospace"
                    to="/SavedNotes"
                  >
                    Saved Notes
                  </Link>
                </li>
                <li className="dropstart">
                  <a
                    className="nav-link dropdown-toggle"
                    href="#"
                    role="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <i
                      style={{ fontSize: "22px" }}
                      className="zmdi zmdi-account-circle"
                    ></i>
                  </a>
                  <ul className="dropdown-menu">
                    <li>
                      <a className="dropdown-item" href="#">
                        {userInfo.name}
                      </a>
                    </li>
                    <li>
                      <a className="dropdown-item" href="#">
                        {userInfo.email}
                      </a>
                    </li>
                    <li>
                      <a className="dropdown-item" href="#">
                        Password
                      </a>
                    </li>
                    <li>
                      <hr className="dropdown-divider" />
                    </li>
                    <li>
                      <a onClick={logoutBtn} className="dropdown-item" href="#">
                        Logout
                      </a>
                    </li>
                  </ul>
                </li>
              </ul>
            </div>
            <div className="p-2 toggleSection">
              <a
                className="toggle_btn"
                type=""
                data-bs-toggle="offcanvas"
                data-bs-target="#offcanvasRight"
                aria-controls="offcanvasRight"
              >
                <i style={{ fontSize: "25px" }} className="zmdi zmdi-menu"></i>
              </a>
              <div
                className="offcanvas offcanvas-end"
                tabIndex="-1"
                id="offcanvasRight"
                aria-labelledby="offcanvasRightLabel"
              >
                <div className="offcanvas-header">
                  <h5 className="offcanvas-title" id="offcanvasRightLabel">
                    {userInfo.name}
                  </h5>
                  <button
                    type="button"
                    className="btn-close"
                    data-bs-dismiss="offcanvas"
                    aria-label="Close"
                  ></button>
                </div>
                <div className="offcanvas-body">
                  <Link
                    className="nav-link text-dark fw-bolder font-monospace"
                    aria-current="page"
                    to="/"
                  >
                    Home
                  </Link>
                  <hr className="dropdown-divider" />
                  <Link
                    className="nav-link text-dark fw-bolder font-monospace"
                    aria-current="page"
                    to="/SavedNotes"
                  >
                    Saved Notes
                  </Link>
                  <hr className="dropdown-divider" />
                  <Link
                    className="nav-link text-dark fw-bolder font-monospace"
                    aria-current="page"
                    to="/SavedNotes"
                  >
                    {userInfo.email}
                  </Link>
                  <hr className="dropdown-divider" />
                  <a
                    onClick={logoutBtn}
                    className="nav-link font-monospace"
                    href="#"
                  >
                    Logout
                  </a>
                </div>
              </div>
            </div>
          </div>
        </nav>
      );
    }
  };

  return <>
    <div className={`cover-spin ${spinner}`}>
      <div className="text-center position-absolute  top-50 start-50 translate-middle">
        <div className="spinner-border" role="status">
          <span className="visually-hidden"></span>
        </div>
        <p>Logout...</p>
      </div>
    </div>
    {
      Routing()
    }
  </>;
};

export default Navbar;
