import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import { SwatchesPicker } from "react-color";
import "./css/CreateNote.css";

const CreateNote = ({ setFooterBottom, authEmail }) => {
  const [visibleNotfi, setVisibleNotfi] = useState('d-none');
  useEffect(() => {
    setFooterBottom({
      marginTop: "13vh",
    });
  }, []);



  const current = new Date();
  const date = `${current.getDate()}/${current.getMonth() + 1
    }/${current.getFullYear()}`;
  const dayNum = current.getDay();
  var day;
  if (dayNum === 0) {
    day = "Sunday";
  } else if (dayNum === 1) {
    day = "Monday";
  } else if (dayNum === 2) {
    day = "Tuesday";
  } else if (dayNum === 3) {
    day = "Wednseday";
  } else if (dayNum === 4) {
    day = "Thursday";
  } else if (dayNum === 5) {
    day = "Friday";
  } else if (dayNum === 6) {
    day = "Sutarday";
  }
  const userEmail = authEmail
  const [inotesObj, setInotesObj] = useState({
    title: "",
    note: "",
    date: "",
    day: "",
    note_Key: "",
    color: "black",
    fontStyle: "",
    fontWeight: "",
    fontSize: "20px",
  });
  const handleChange = (e) => {
    const { name, value } = e.target;
    setInotesObj({
      ...inotesObj,
      [name]: value,
      date: date,
      day: day,
      note_Key: userEmail
    });
  };

  const [textStyle, setTextStyle] = useState({
    color: "black",
    fontStyle: "",
    fontWeight: "",
    fontSize: "20px",
    backgroundColor: "",
  });

  const [displayPallete, setDisplayPallete] = useState("none");

  const add = () => {
    axios
      .post("/api/createNote", inotesObj)
      .then((res) => {
        if (res.status === 200) {
          setInotesObj({
            title: "",
            note: "",
          });
          setVisibleNotfi('d-block');
          setTimeout(() => {
            setVisibleNotfi('d-none')
          }, 2000);
        } else {
          alert("Note not saved");
        }
      });
  };

  return (
    <>
      <div className={`container ${visibleNotfi}`} style={{ zIndex: "1000", position: "fixed", maxWidth: "100%", margin: "0" }} >
        <div className="saved_toast align-items-center" role="alert" aria-live="assertive" aria-atomic="true">
          <div className="toast-body">
            Note Saved!
          </div>
        </div>
      </div>


      <div className="container edit_menu_up">
        <div className=" d-flex justify-content-between">
          <div className="mx-3">
            <a
              href="#/"
              className="edit_menu_italic"
              onClick={(e) => {
                if (textStyle.fontStyle === "italic") {
                  setTextStyle({
                    ...textStyle,
                    fontStyle: "",
                  });
                  setInotesObj({
                    ...inotesObj,
                    fontStyle: "",
                  });
                } else {
                  setTextStyle({
                    ...textStyle,
                    fontStyle: "italic",
                  });
                  setInotesObj({
                    ...inotesObj,
                    fontStyle: "italic",
                  });
                }
                console.log(textStyle.fontStyle);
              }}
            >
              <i>Italic</i>
            </a>
          </div>
          <div>
            <a
              href="#/"
              className="edit_menu_bold"
              onClick={() => {
                if (textStyle.fontWeight === "bold") {
                  setTextStyle({
                    ...textStyle,
                    fontWeight: "",
                  });
                  setInotesObj({
                    ...inotesObj,
                    fontWeight: "",
                  });
                } else {
                  setTextStyle({
                    ...textStyle,
                    fontWeight: "bold",
                  });
                  setInotesObj({
                    ...inotesObj,
                    fontWeight: "bold",
                  });
                }
              }}
            >
              <b>Bold</b>
            </a>
          </div>
          <div className="mx-3">
            <a
              href="#/"
              className="edit_menu_color"
              onMouseEnter={() => {
                setDisplayPallete("block");
              }}
              onMouseLeave={() => {
                setDisplayPallete("none");
              }}
              style={{ fontWeight: "500", zIndex: "1" }}
            >
              <font color="red">C</font>
              <font color="green">o</font>
              <font color="blue">l</font>
              <font color="black">o</font>
              <font color="brown">r</font>
              <div style={{ display: displayPallete }}>
                <div
                  style={{
                    display: "block",
                    position: "absolute",
                    marginLeft: "-161px",
                  }}
                >
                  <SwatchesPicker
                    height="180px"
                    width="350px"
                    color={textStyle}
                    onChangeComplete={(color) => {
                      setTextStyle({
                        ...textStyle,
                        color: color.hex,
                      });
                      setInotesObj({
                        ...inotesObj,
                        color: color.hex,
                      });
                      console.log(color.hex);
                    }}
                  />
                </div>
              </div>
            </a>
          </div>
          <div className="mx-3">
            <i className="zmdi zmdi-format-size me-2"></i>
            <a href="#/" className="edit_menu_font">
              <input
                className="edit_inputFont"
                onChange={(e) => {
                  setTextStyle({
                    ...textStyle,
                    fontSize: e.target.value + "px",
                  });
                  setInotesObj({
                    ...inotesObj,
                    fontSize: e.target.value + "px",
                  });
                }}
                type="number"
                defaultValue={20}
                step={2}
                max={40}
                min={0}
              />
            </a>{" "}
            px
          </div>
        </div>
      </div>

      {/* ------------------------creteNote ----------------- */}
      <div className="createNote">
        <div className="d-flex justify-content-between">
          <input
            type="text"
            className="inpBox title-inp text-trnf"
            name="title"
            placeholder="Add Title"
            onChange={handleChange}
            value={inotesObj.title}
          />
          <p value={inotesObj.date} className="currentDate">
            {day} - {date}
          </p>
        </div>
        <textarea
          style={textStyle}
          name="note"
          className="note-dec"
          placeholder="Add a description"
          onChange={handleChange}
          value={inotesObj.note}
        ></textarea>

        <div className="addBtn" onClick={add} >
          Save
        </div>

      </div>




      <div id="mySidenav" className="sidenav edit_menu_side">
        <a
          href="#/"
          id="italic"
          style={{ display: "flex" }}
          onClick={(e) => {
            if (textStyle.fontStyle === "italic") {
              setTextStyle({
                ...textStyle,
                fontStyle: "",
              });
              setInotesObj({
                ...inotesObj,
                fontStyle: "",
              });
            } else {
              setTextStyle({
                ...textStyle,
                fontStyle: "italic",
              });
              setInotesObj({
                ...inotesObj,
                fontStyle: "italic",
              });
            }
            console.log(textStyle.fontStyle);
          }}
        >
          <i>Italic</i>
        </a>

        <a
          href="#/"
          id="bold"
          onClick={() => {
            if (textStyle.fontWeight === "bold") {
              setTextStyle({
                ...textStyle,
                fontWeight: "",
              });
              setInotesObj({
                ...inotesObj,
                fontWeight: "",
              });
            } else {
              setTextStyle({
                ...textStyle,
                fontWeight: "bold",
              });
              setInotesObj({
                ...inotesObj,
                fontWeight: "bold",
              });
            }
          }}
        >
          <b>Bold</b>
        </a>

        <a
          href="#/"
          id="color"
          onMouseEnter={() => {
            setDisplayPallete("block");
          }}
          onMouseLeave={() => {
            setDisplayPallete("none");
          }}
          style={{ fontWeight: "500", zIndex: "1" }}
        >
          <font color="red">C</font>
          <font color="green">o</font>
          <font color="blue">l</font>
          <font color="black">o</font>
          <font color="brown">r</font>
          <div style={{ display: displayPallete }}>
            <SwatchesPicker
              color={textStyle}
              onChangeComplete={(color) => {
                setTextStyle({
                  ...textStyle,
                  color: color.hex,
                });
                setInotesObj({
                  ...inotesObj,
                  color: color.hex,
                });
                console.log(color.hex);
              }}
            />
          </div>
        </a>
        <a href="#/" id="contact">
          <input
            className="inputFont"
            onChange={(e) => {
              setTextStyle({
                ...textStyle,
                fontSize: e.target.value + "px",
              });
              setInotesObj({
                ...inotesObj,
                fontSize: e.target.value + "px",
              });
            }}
            type="number"
            defaultValue={20}
            step={2}
            max={40}
            min={0}
          />
        </a>
      </div>


    </>
  );
};

export default CreateNote;
