import "./css/Login.css";
import React from "react";
import { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";


const Login = () => {
  const navigate = useNavigate();
  const [spinner, setSpinner] = useState('d-none');
  const [userInfo, setUserInfo] = useState({
    email: "",
    password: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserInfo({
      ...userInfo,
      [name]: value,
    });

  };

  const login = async () => {
    try {
      setSpinner('d-block')
      const res = await axios.post("/api/login", userInfo);
      console.log(res);
      document.cookie = `jwtoken=${res.data};expires=${new Date(
        Date.now() + 25892000000
      )};path=/`;
      if (!res.data) {
        setSpinner('d-none')
        alert("wrong password or email");
      } else {
        setSpinner('d-none')
        navigate("/");
        window.location.reload();
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <>
      <div className={`cover-spin ${spinner}`}>
        <div className="text-center position-absolute  top-50 start-50 translate-middle">
          <div className="spinner-border" role="status">
            <span className="visually-hidden"></span>
          </div>
          <p>Login...</p>
        </div>
      </div>

      <div className="container">
        <div id="start-spin"></div>
        <div className="login">
          <h1>login</h1>
          <input
            name="email"
            onChange={handleChange}
            className="loginInp"
            value={userInfo.email}
            type="text"
            placeholder="Enter your Email"
          />
          <input
            name="password"
            onChange={handleChange}
            className="loginInp"
            value={userInfo.password}
            type="password"
            placeholder="Enter your password"
          />
          <div onClick={login} className="btn my-4">
            Login
          </div>
          <p>or</p>
          <a onClick={() => navigate("/register")} className="mb-4" style={{ cursor: "pointer" }} >
            Register &rArr;
          </a>
        </div>
      </div>
    </>
  );
};

export default Login;
